} 

.courses-drop li{
    padding: 4px 0px 0px 30px;
    margin: 0px;
    font-size: 20px;    
}

/* end of side bar styling*/

/* course detail diplay porting styling */
.details{
    display: flex;
    float: right;
    height: 597px;
    padding-left: 100px;
    text-align: left;
    overflow: scroll;
}
/* course detail diplay porting styling end */

/*Course Detail styling*/

.start-btn{
    margin: 0px;
    
}

.course-detail h1{
    color: #0288d1; 
}

.right-aligner{
    width: 250px;
    text-align: left;   
}

.detail-header{
    padding: 0px;
    float: left;
    
}

.text-block{
    text-align: left;
    padding-left: 10px
}
/*Course Detail styling end*/
/* media query */
@media only screen and (max-width: 600px){
    .quiz-tree{
        height: auto;
        background-color: white;
        padding-left: 10px;
        padding-right: 10px;
    }
    .dashboard strong{
        font-size: 25px;
        padding: 0px;
    }   
    .navbar{
        height: 2.8em;
    }
    .dashboard{
        padding-left: 10px;
        position: absolute;
    }
    .dashboard i{
        font-size: 25px;
        padding: 0px;
        padding-top: 7px; 
        width:10px
    }
    .logout-btn {
        padding: 0px;
    }
    .logout-btn button{
        font-size: 20px;
    }
    .quiz-tree-wrapper{
        width: 10px;   
        border-radius: 20px;  
        
    }
    .quiz-tree-wrapper .row{
        margin-left: 5px;

    }
    .quiz-tree .quiz-tree-wrapper:first-child{
        padding-top: 2px
    }
    .text-block{
        padding-left: 0px ;
    }
    .right-aligner{
        width: 190px
    }
    .details{
        padding-left: 10px;
    }
}
@media only screen and (min-width: 768px){
    .quiz-tree{
        height: 579px;
     
    }
}
@media only screen and (min-width: 992px){
    .quiz-tree{
        height: 579px;
        
    }
    .quiz-tree-courses{
        font-size: 15px;
    }
    .courses-drop li{
        font-size: 13px;
    }
}
@media only screen and (min-width: 1200px){
    .quiz-tree{
        height: 579px;
    }
    .quiz-tree-courses{
        font-size: 20px;
    }
    .courses-drop li{
        font-size: 20px;
    }
}
h1, h5 {
    float: left;
}

h1{
    color: #0288d1;
}

.ans-block{
    background-color: #0288d1;
    color: white;   
    display: flex;
    justify-content: center;
    justify-items: center;
    text-align: center;
    margin: 0px;
    font-family: Arial, Helvetica, sans-serif
}

.res-block{
    display: flex;
    justify-content: center;
    justify-items: center;
    text-align: center;
    border-bottom: 2px solid grey;
    border-right: 2px solid grey;
    border-left: 2px solid grey;
}

.res-block>h5{
    font-size: 35px; 
}

.ans-block>h5{
    font-size: 35px
}

.set-width-right{
    width: 190px;
}

.result-contents>.text-block{
    padding-left: 10px;
    display: inline-block;
}
.quiz-contents{
    display: flex;
    justify-content: center;
    justify-items: center;
    overflow: hidden;
}

.btn-container{
    justify-content: flex-end;
    display: flex;
}

.question-options{
    width: 70%;
    -webkit-transform: translate(30%, 0%);
            transform: translate(30%, 0%);
    font-size: 20px;
    border: 1px solid red
}

.quiz-question {
    border: 1px solid red;
    position: absolute;
    top: 30%;
    width: 60%;
    text-align: center;
    flex-direction: column;
    display: flex;
    justify-content: center;
    
}
@media only screen and (max-width: 600px){
    .question-options{
        width: 90%;
        -webkit-transform: translate(0%, 0%);
                transform: translate(0%, 0%);
    }
    .quiz-question{
        width: 100%;
        top: 20%;
        border: 1px solid red;
        padding: 0px 15px 0px 15px;
    }
    .quiz-contents>h1{
        font-size: 30px;
        width: 100%
    }
}

